.ticket_view_box {
    padding: 25px 25px 30px 25px;
}

.ticket_viewBtn_group {
    border-radius: 5px;
    border: 1px solid #eef0f4;
    background-color: #fff;
    padding: 14px 15px;
    .view_input {
        border: 0;
        height: 30px;
        padding: 10px 10px;
        font-size: 14px;
        font-weight: normal;
        color: var(--text_color);
        &::placeholder {
            color: $body_text;
        }
    }
    .file_btn {
        cursor: pointer;
        border: 0;
        background: transparent;
        padding: 0;
        margin-left: 31px;
        i {
            color: #687083;
            font-size: 14px;
            font-weight: 900;
        }
        input {
            opacity: 0;
        }
        &::before {
            content: "";
            position: absolute;
            right: -15px;
            height: 15px;
            width: 1px;
            background: #eef0f4;
            top: 50%;
            transform: translateY(-50%);
        }
    }
}

// user_wrapper_lists
.user_wrapper_lists {
    .user_wrapper_list {
        grid-gap: 12px;
        border-bottom: 1px solid #eef0f4;
        padding: 20px 0;
        &:first-child {
            border-top: 1px solid #eef0f4;
        }
        .user_wrapper_left {
            flex: 66px 0 0;
        }
    }
}