.theme_btn {
    background: var(--text_color)4;
    @include border-radius(5px);
    display: inline-block;
    font-family: $font1;
    font-size: 14px;
    color: $white;
    font-weight: 500;
    padding: 13.5px 47px;
    border: 1px solid transparent;
    text-transform: capitalize;
    display: inline-block;
    transition: 0.3s;
    cursor: pointer;
    &.small_btn {
        padding: 13.5px 25px;
    }
    &.large_btn {
        padding: 18.5px 30px;
    }
    &.small_btn2 {
        padding: 13.5px 39px;
    }
    &.small_btn3 {
        padding: 13.5px 32px;
    }
    &.small_btn4 {
        padding: 11px 21px;
    }
    &.small_btn5 {
        padding: 8.5px 20px;
    }
    @media #{$small_mobile} {
        padding: 15px 16px;
    }
    &.green_btn {
        background: #54c254 !important;
        padding: 8.5px 18px;
        color: #fff !important;
        border-color: #54c254 !important;
        font-size: 16px;
    }
    &.width_120px {
        width: 120px;
        padding: 13.5px 7px;
        text-align: center;
    }
    &.width_210px {
        min-width: 210px;
        padding: 13.5px 20px;
        text-align: center;
    }
    &:hover {
        color: var(--text_color)4;
        border-color: var(--text_color)4;
        background: transparent;
    }
}

.theme_btn2 {
    background: transparent;
    @include border-radius(0px);
    display: inline-block;
    font-family: $font2;
    font-size: 14px;
    color: var(--base_color);
    font-weight: 600;
    padding: 16px 42px;
    border: 1px solid var(--base_color);
    text-transform: uppercase;
    display: inline-block;
    transition: 0.3s;
    cursor: pointer;
    letter-spacing: 2.24px;
    @media #{$small_mobile} {
        padding: 13px 30px;
    }
    &.width_120px {
        width: 120px;
        padding: 13.5px 7px;
        text-align: center;
    }
    &:hover {
        color: $white;
        border-color: var(--base_color);
        background: var(--base_color);
    }
}

