.dashboard_notification_box {
    display: flex;
    grid-gap: 20px;
    &:not(:last-child) {
        padding-bottom: 25px;
        margin-bottom: 30px;
        border-bottom: 1px solid #e3e6ef;
    }
    .nofication_userName {
        width: 50px;
        height: 50px;
        display: flex;
        align-items: center;
        justify-content: center;
        line-height: 50px;
        border-radius: 50%;
        background: #525ce5;
        color: $white;
        flex: 50px 0 0;
    }
    .nofication_content {
        h4 {
            color: #202e3b;
        }
        .nofication_subText {
            font-size: 14px;
            line-height: 24px;
            font-weight: 500;
            margin: 2px 0 14px 0;
            span {
                color: #00124e;
            }
        }
        .nofication_desc {
            font-size: 14px;
            line-height: 24px;
            font-weight: 400;
        }
    }
}

.amazySwitch_toggle {
    display: inline-block;
    height: 20px;
    position: relative;
    width: 40px;
    margin-bottom: 0;
    input {
        opacity: 0;
    }
    .slider:before {
        bottom: 0px;
        content: "";
        height: 20px;
        right: 0px;
        position: absolute;
        -webkit-transition: 0.4s;
        transition: 0.4s;
        width: 20px;
        background: #707db7;
        box-shadow: none;
        top: 0;
        box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    }
    input:checked+.slider {
        background: #eceef4;
    }
    input:checked+.slider:before {
        transform: translateX(20px);
        background: var(--base_color);
        box-shadow: 0 2px 5px rgba(0, 0, 0, 0.16);
    }
    .slider.round {
        border-radius: 34px;
    }
    .slider.round:before {
        border-radius: 50%;
    }
    .switch_toggle input {
        display: none;
    }
    .slider {
        background-color: #eceef4;
        bottom: 0;
        cursor: pointer;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
        transition: 0.4s;
    }
}