.cart_wrapper {
    padding: 97px 0 70px 0;
    @media #{$tab} {
        padding: 60px 0 50px 0;
    }
    .cart_table_wrapper {
        h4 {
            font-size: 22px;
            font-weight: 700;
            color: var(--text_color);
            margin-bottom: 30px;
        }
    }
}

.table.custom_table {
    border-radius: 5px;
    border-collapse: collapse;
    // border-style: hidden;
    thead {
        tr {
            th {
                font-size: 16px;
                font-weight: 700;
                color: var(--text_color);
                border-top: 0;
                border-bottom: 1px solid var(--border_color) !important;
                padding: 0 30px 19px 30px;
                font-family: $font1;
                &:nth-child(1) {
                    // border-left: 1px solid #E1E2E6;
                    padding-right: 230px;
                    @media #{$tab} {
                        padding-right: 180px;
                    }
                }
                &:last-child {
                    // border-left: 1px solid #E1E2E6;
                }
            }
        }
    }
    tbody {
        tr {
            td {
                border-bottom: 1px solid var(--border_color);
                font-family: $font1;
                color: var(--text_color);
                font-size: 16px;
                vertical-align: middle;
                padding: 20px 30px;
                white-space: nowrap;
                .in_stoke {
                    color: #49a60a;
                }
                &:nth-child(1) {
                    // border-right: 1px solid #E1E2E6;
                    padding-right: 20px;
                }
                &:last-child {
                    // border-left: 1px solid #E1E2E6;
                }
                .close_icon {
                    margin-left: 10px;
                    cursor: pointer;
                    &.style_2 {
                        margin-right: 20px;
                        margin-left: 0;
                        position: relative;
                        top: -2px;
                    }
                }
                .product_name {
                    .thumb {
                        width: 160px;
                        height: 100px;
                        margin-left: 40px;
                        @media #{$tab} {
                            width: 100px;
                            height: 60px;
                            margin-left: 30px;
                        }
                        img {
                            width: 100%;
                        }
                    }
                    span {
                        font-size: 16px;
                        font-weight: 400;
                        color: var(--text_color);
                    }
                }
            }
        }
    }
    tfoot {
        td {
            border-top: 1px solid var(--border_color);
            border-bottom: 1px solid var(--border_color);
            padding: 25px 20px;
            &:nth-child(1) {
                border-right: 1px solid var(--border_color);
            }
            &:last-child {
                border-left: 1px solid var(--border_color);
                text-align: left;
            }
        }
    }
    &.custom_table2 {
        tbody {
            td {
                padding: 25px 40px;
            }
        }
    }
}

