.coupon_wrapper {
    display: grid;
    grid-template-columns: auto 100px;
    grid-gap: 5px;
    .theme_btn.small_btn2 {
        padding: 13.5px 9px;
    }
    .primary_input {
        background: #f9f9fd;
        font-size: 14px;
        font-weight: 500;
        font-family: $font1;
        color: var(--text_color);
        &::placeholder {
            color: #687083;
            font-size: 14px;
            font-weight: 500;
            font-family: $font1;
        }
    }
}

.product_number_count {
    display: inline-flex;
    .count_single_item {
        width: 40px;
        height: 50px;
        border-radius: 0px;
        border: 1px solid #e1e2e6;
        font-size: 14px;
        color: $home_body;
        display: inline-block;
        text-align: center;
        line-height: 50px;
        transition: 0.3s;
        cursor: pointer;
        background: #f9f9fd;
    }
    .count_single_item.input-number {
        cursor: text;
        width: 60px;
        background: #fff;
        color: var(--text_color);
        font-weight: 500;
        font-family: $font1;
        font-size: 16px;
        border-right: 0;
        border-left: 0;
        &::placeholder {
            color: var(--text_color);
        }
    }
    &.style_2 {
        .count_single_item {
            width: 30px;
            height: 30px;
            line-height: 30px;
        }
        .count_single_item.input-number {
            width: 30px;
            &::placeholder {
                color: var(--text_color);
            }
        }
    }
    &.style_3 {
        .count_single_item {
            width: 40px;
            height: 40px;
            line-height: 40px;
        }
        .count_single_item.input-number {
            width: 60px;
            &::placeholder {
                color: var(--text_color);
            }
        }
    }
    &.style_4 {
        .count_single_item {
            width: 40px;
            height: 40px;
            line-height: 40px;
        }
        .count_single_item.input-number {
            width: 50px;
            &::placeholder {
                color: var(--text_color);
            }
        }
    }
}

.table.position_table {
    border-radius: 5px;
    border-collapse: collapse;
    // border-style: hidden;
    thead {
        tr {
            th {
                font-size: 18px;
                font-weight: 600;
                color: var(--text_color);
                border-top: 0;
                border-bottom: 0;
