                font-family: $font1;
                background: var(--base_color);
                color: #fff;
                h5 {
                    color: #fff;
                    font-size: 18px;
                }
                .job_lists {
                    color: #fff;
                    font-size: 18px;
                    margin-bottom: 5px;
                    display: block;
                }
            }
        }
    }
    tbody {
        tr {
            td {
                .job_name {
                    font-size: 16px;
                    font-weight: 600;
                    color: var(--text_color);
                    margin-bottom: 3px;
                }
                .job_position {
                    font-size: 14px;
                    font-weight: 400;
                    color: #676e8b;
                }
                // font-family: $font1;
                // color: var(--text_color);
                // font-size: 16px;
                vertical-align: middle;
                padding: 20px 0px 20px 30px;
                white-space: nowrap;
                &:nth-child(1) {
                    border-top: 1px solid #e1e2e6;
                }
                &:last-child {
                    border-top: 1px solid #e1e2e6;
                }
            }
        }
    }
}

.cart_sub_box {
    border: 1px solid var(--border_color);
    background-color: #fafafa;
    padding: 30px;
    .primary_checkbox {
        margin: 24px 0 23px 0;
    }
    .cart_sub_box_amount {
        border-bottom: 1px solid #ededed;
        padding-bottom: 21px;
        p {
            font-size: 14px;
            font-weight: 500;
            color: $body_text;
            display: flex;
            justify-content: space-between;
            align-items: center;
            &:not(:last-child) {
                margin-bottom: 4px;
            }
            span {
                font-size: 16px;
                font-weight: 700;
                color: var(--text_color);
                font-family: $font1;
            }
        }
    }
    .cart_sub_box_total {
        display: flex;
        justify-content: space-between;
        border-bottom: 1px solid var(--border_color);
        padding: 23px 0 22px 0;
        h4 {
            font-size: 14px;
            font-weight: 700;
            color: var(--text_color);
        }
    }
}

.sheping_grid_box {
    display: grid;
    grid-template-columns: 525px 450px;
    grid-gap: 75px;
    align-items: flex-end;
    padding-top: 120px;
    @media #{$tab2} {
        padding-top: 50px;
        grid-template-columns: auto;
        grid-gap: 40px;
    }
    @media #{$large_xl} {
        grid-gap: 30px;
        grid-template-columns: 480px 450px;
    }
    @media #{$lg_device} {
        grid-gap: 30px;
        grid-template-columns: 425px 380px;
    }
    .cart_table_wrapper {
        border-left: 1px solid #ebebeb;
        padding-left: 75px;
        margin-bottom: 0;
        @media #{$tab2} {
            border: 0;
            padding-left: 0;
        }
        @media #{$large_xl} {
            padding-left: 30px;
        }
        @media #{$lg_device} {
            padding-left: 30px;
        }
    }
}

.coupon_verify_information {
    border-radius: 5px;
    border: 1px solid #29c489;
    padding: 14px 20px 15px 20px;
    grid-gap: 25px;
    &.style2 {
        border: 1px solid #fd2121;
        grid-gap: 20px;
        .coupon_content {
            h4 {
                color: #fd2121;
                margin-bottom: 8px;
            }
            p {
                font-size: 14px;
                line-height: 1.29;
                font-weight: 400;
                color: $body_text;
            }
        }
    }
    .coupon_content {
        h4 {
            color: #29c489;
            margin-bottom: 2px;
        }
        .remove_coupon {
            font-size: 12px;
            font-weight: bold;
            color: #ff2121;
        }
    }
}

