.popular_category {
    padding: 95px 0 0 0;
    @media #{$tab} {
        padding: 75px 0 0 0;
    }
    .single_carousel {
        .thumb {
            min-height: 86px;
            display: flex;
            align-items: flex-end;
            justify-content: center;
            img {
                width: auto;
                display: inline-block;
            }
        }
        h4 {
            margin: 17px 0 1px 0;
        }
    }
}

.Featured_Products_area {
    padding: 96px 0 70px 0;
    @media #{$tab} {
        padding: 74px 0 50px 0;
    }
}

.topSelling_Products_area {
    padding: 0px 0 65px 0;
    @media #{$tab} {
        padding: 0px 0 40px 0;
    }
}

// home 4 category
.home4_category_area {
    .home4_catwiz {
        background: #b8da97;
        border-radius: 10px;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
        padding: 55px 30px 52px 30px;
        &.catwiz_bg2 {
            background: #83cccf;
        }
        &.catwiz_bg3 {
            background: #dfa2a9;
        }
        &.catwiz_bg4 {
            background: #9aaddc;
        }
        .icon {
            background: #fff;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 auto;
        }
        h4 {
            font-size: 22px;
            font-weight: 700;
            color: var(--text_color);
            margin: 25px 0 11px 0;
        }
        span {
            font-size: 14px;
            font-family: $font1;
            color: $home_body;
            font-weight: 400;
            text-transform: uppercase;
            display: block;
            margin-bottom: 27px;
        }
        .shop_text {
            font-size: 14px;
            color: var(--base_color);
            text-transform: uppercase;
            font-weight: 700;
            display: inline-block;
        }
    }
}

.home6_cat1_bg {
    @include background("../img/home_6/category/cat_1.png");
}

.home6_cat2_bg {
    @include background("../img/home_6/category/cat_2.png");
}

.home6_cat3_bg {
    @include background("../img/home_6/category/cat_3.png");
}

.home6_cat4_bg {
    @include background("../img/home_6/category/cat_4.png");
}

.home6_cat5_bg {
    @include background("../img/home_6/category/cat_5.png");
}

.home6_cat6_bg {
    @include background("../img/home_6/category/cat_6.png");
}

.home6_category_wiz {
    padding: 36px 30px;
    span {
        font-size: 20px;
        font-weight: 700;
        color: var(--text_color);
        transition: 0.3s;
        &:hover {
            color: var(--base_color);
        }
    }
    p {
        font-size: 14px;
        font-weight: 500;
        color: $home_body;
        margin: 1px 0 28px 0;
    }
    h5 {
        font-size: 14px;
        font-weight: 600;
        color: var(--base_color);
        text-transform: uppercase;
        margin-bottom: 0;
    }
}

// home 7 category
.home7_category_area {
    .home4_catwiz {
        background: #fff;
        border-radius: 20px;
        box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
        padding: 60px 30px 48px 30px;
        &.catwiz_bg2 {
            .icon {
                background: #c2ecee;
            }
        }
        &.catwiz_bg3 {
            .icon {
                background: #f2ced2;
            }
        }
        &.catwiz_bg4 {
            .icon {
                background: #d8e2f7;
            }
        }
        .icon {
            background: #e1f0d3;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 auto;
        }
        h4 {
            font-size: 20px;
            font-weight: 800;
            color: var(--text_color);
            margin: 30px 0 10px 0;
            transition: 0.3s;
            &:hover {
                color: var(--base_color);
            }
        }
        span {
            font-size: 14px;
            font-family: $font1;
            color: $home_body;
            font-weight: 500;
            text-transform: uppercase;
            display: block;
            margin-bottom: 32px;
        }
    }
}