.slick_custom_container {
    .item-slick.slick-slide.slick-current.slick-active {
        outline: none !important;
    }
    .slider-for {
        margin-bottom: 10px;
    }
    .slider-for img {
        width: 100% !important;
        min-height: 100%;
        object-fit: cover;
    }
    .slider-nav {
        margin: auto -5px;
    }
    .slider-nav .item-slick {
        max-width: 240px;
        margin-left: 0px;
        outline: none !important;
        cursor: pointer;
        margin: 0 5px;
        opacity: 0.8;
        &.slick-current {
            opacity: 1;
        }
    }
    .slider-nav .item-slick img {
        max-width: 100%;
        background-size: cover;
        background-position: center;
        width: 100%;
    }
    .slick-arrow {
        position: absolute;
        top: 50%;
        z-index: 50;
        transform: translateY(-50%);
    }
    .slick-prev {
        right: 0px;
    }
    .slick-next {
        left: 0px;
    }
    .slider-nav {
        button {
            padding: 0;
            border: 0;
            width: 30px;
            height: 30px;
            display: flex !important;
            align-items: center;
            justify-content: center;
            border-radius: 50%;
            font-size: 15px;
            color: #fff;
            background: var(--base_color);
            opacity: 0;
            transition: 0.3s;
            @media #{$small_mobile} {
                display: none !important;
            }
            &:hover {
                background: var(--text_color);
            }
        }
        &:hover {
            .slick-prev {
                right: 20px;
            }
            .slick-next {
                left: 20px;
            }
            button {
                opacity: 1;
            }
        }
    }
}