.product_details_wrapper {
    padding: 80px 0 0px 0;
    @media #{$tab} {
        padding: 15px 0 0px 0;
    }
    .products_imgs {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 10px;
        .single_product_image {
            overflow: hidden;
            img {
                width: 100%;
                transition: 0.3s;
                transform: scale(1);
            }
            &:hover {
                img {
                    transform: scale(1.3);
                }
            }
        }
    }
    .product_content_details {
        .stoke_badge {
            font-size: 12px;
            font-weight: 700;
            background: #13c481;
            color: $white;
            border-radius: 2px;
            padding: 4px 7px;
        }
        h3 {
            font-size: 24px;
            font-weight: 700;
            color: var(--text_color);
            line-height: 1.42;
            max-width: 450px;
            margin: 13px 0 9px 0;
            @media #{$tab} {
                font-size: 20px;
            }
        }
        .details_text {
            margin: 24px 0 24px 0;
        }
        .viendor_text {
            grid-gap: 30px;
            .stock_text {
                &:not(:last-child) {
                    &::before {
                        content: "";
                        position: absolute;
                        left: -15px;
                        height: 15px;
                        width: 1px;
                        background: #f1ece8;
                        top: 50%;
                        transform: translateY(-50%);
                    }
                }
            }
        }
        .stock_text {
            font-size: 14px;
            font-weight: 500;
            color: var(--text_color);
            margin-bottom: 0px;
            position: relative;
            span {
                color: $home_body;
                font-weight: 500;
            }
        }
        .destils_prise_information_box {
            border-top: 1px solid var(--border_color);
            border-bottom: 1px solid var(--border_color);
            padding: 21px 0 14px 0;
            .pro_details_prise {
                grid-gap: 10px;
                span {
                    font-size: 24px;
                    font-weight: 500;
                    color: #00124e;
                }
            }
            .pro_details_disPrise {
                margin: 1px 0 9px 0;
                .discount_prise {
                    display: flex;
                    align-items: center;
                    font-size: 16px;
                    color: $home_body;
                    font-weight: 500;
                    grid-gap: 5px;
                }
                .diccount_percents {
                    border-radius: 5px;
                    border: 1px solid var(--border_color);
                    background-color: #f9f9fd;
                    font-size: 12px;
                    font-weight: 500;
                    color: $home_body;
                    height: 21px;
                    min-width: 36px;
                    text-align: center;
                    display: inline-flex;
                    align-items: center;
                    justify-content: center;
                }
            }
            .pro_details_text {
                font-size: 14px;
                font-weight: 500;
                color: $home_body;
            }
        }
        .prise_text {
            font-size: 24px;
            font-family: $font1;
            font-weight: 700;
            color: var(--base_color);
            grid-gap: 8px;
            span {
                font-size: 14px;
                font-weight: 400;
                color: $home_body;
                font-family: $font1;
            }
        }
        .size_quide_info {
            grid-gap: 20px;
            .single_side_guide {
                font-size: 12px;
                color: var(--base_color);
                font-weight: 700;
                display: inline-flex;
                align-items: center;
                grid-gap: 10px;
                text-transform: uppercase;
                &:last-child {
                    margin-left: 0;
                    margin-right: auto;
                    @media #{$small_mobile} {
                        margin-left: auto;
                        margin-right: 0;
                    }
                }
            }
        }
        .add_wish_compare {
            grid-gap: 30px;
            .single_wish_compare {
                font-size: 12px;
                color: var(--base_color);
                font-weight: 700;
                display: inline-flex;
                align-items: center;
                grid-gap: 10px;
                i {
                    font-size: 16px;
                }
            }
        }
        .product_ratings {
            display: flex;
            align-items: center;
            margin: 9px 0 17px 0;
            .stars {
                display: flex;
                align-items: center;
                margin-left: 15px;
                i {
                    font-size: 15px;
                    color: #ffc107;
                    &:not(:last-child) {
                        margin-left: 4px;
                    }
                }
            }
            span {
                font-size: 14px;
                font-weight: 500;
                color: $home_body;
            }
        }
        .product_info {
            .single_pro_varient {
                margin-bottom: 20px;
                display: flex;
                align-items: center;
                grid-gap: 10px;
                @media #{$tab} {
                    flex-wrap: wrap;
                }
                .color_List {
                    label {
                        &:not(:last-child) {
                            margin-left: 10px;
                        }
                    }
                }
                &.style_2 {
                    grid-gap: 20px;
                    .dot_devide {
                        margin-right: 11px;
                    }
                }
                .product_size {
                    display: flex;
                    align-items: center;
                    li {
                        font-size: 16px;
                        font-weight: 600;
                        color: var(--text_color);
                        border: 1px solid #e1e2e6;
                        width: 40px;
                        height: 40px;
                        border-radius: 5px;
                        text-align: center;
                        line-height: 38px;
                        text-transform: uppercase;
                        flex: 40px 0 0;
                        cursor: pointer;
                        font-family: $font1;
                        &.active {
                            border-color: var(--base_color);
                            background: var(--base_color);
                            color: $white;
                        }
                        &:not(:last-child) {
                            margin-left: 10px;
                        }
                    }
                    &.material_lists {
                        li {
                            padding: 0 15px;
                            font-weight: 400;
                            flex: auto 0 0;
                            width: auto;
                            text-transform: capitalize;
                            font-size: 16px;
                        }
                    }
                    &.material_list2 {
                        li {
                            width: 50px;
                            height: 50px;
                            flex: 50px 0 0;
                            padding: 4px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            &.active {
                                border: 2px solid var(--base_color);
                                background: $white;
                                color: $white;
                                padding: 3px;
                            }
                            img {
                                width: 100%;
                                border-radius: 5px;
                            }
                        }
                    }
                }
            }
            .add_wishCard {
                .add_wish_list {
                    font-size: 14px;
                    font-weight: 600;
                    color: $home_body;
                    display: inline-flex;
                    align-items: center;
                    i {
                        font-size: 22px;
                        color: #090b17;
                        margin-left: 15px;
                    }
                }
            }
        }
        .pro_branding_info {
            border-top: 1px solid var(--border_color);
            padding-top: 28px;
            margin-top: 40px;
            .single_branding {
                display: grid;
                grid-template-columns: 79px auto;
                grid-gap: 15px;
                margin-bottom: 1px;
                &:last-child {
                    margin-top: 8px;
                }
                span {
                    font-size: 14px;
                    font-weight: 600;
                    color: var(--text_color);
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    text-transform: uppercase;
                }
                p {
                    font-size: 16px;
                    color: $home_body;
                    font-weight: 400;
                }
            }
        }
    }
    .product_details_sujjetion {
        border-top: 1px solid var(--border_color);
        padding-top: 18px;
        .product_details_sujjetion_box {
            border: 1px solid var(--border_color);
            .product_details_sujjetion_single {
                padding: 10px;
                border-bottom: 1px solid var(--border_color);
                .thumb {
                    width: 50px;
                    height: 50px;
                    flex: 50px 0 0;
                }
                .product_details_sujjetion_content {
                    h4 {
                        margin-bottom: 4px;
                    }
                }
            }
            .product_details_sujjetion_total {
                padding: 12px;
                .product_details_sujjetion_left {
                    span {
                        margin-bottom: 6px;
                    }
                }
            }
        }
    }
    .social_links {
        display: flex;
        align-items: center;
        li {
            &:not(:last-child) {
                margin-left: 10px;
            }
            a {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                color: #fff;
                text-align: center;
                line-height: 30px;
                font-size: 12px;
                transition: 0.3s;
                &:hover {
                    background: var(--text_color)4 !important;
                }
                &.facebook_bg {
                    background: #3b5998;
                }
                &.twitter_bg {
                    background: #1da1f2;
                }
                &.linkedin_bg {
                    background: #dd5143;
                }
            }
        }
    }
}

// product_des_review_wrapper
.product_des_review_wrapper {
    .shop_tab {}
    .shop_tab_content {
        .description_content {
            padding: 30px 0 80px 0;
            p {
                font-size: 16px;
                font-weight: 400;
                line-height: 30px;
                color: var(--text_color);
            }
            .Specifications {
                h4 {
                    font-size: 22px;
                    font-weight: 600;
                    color: var(--text_color);
                    margin: 52px 0 30px 0;
                }
                .Specifications_list {
                    .single_specif {
                        display: grid;
                        grid-template-columns: 152px auto;
                        grid-gap: 30px;
                        margin-bottom: 13px;
                        span {
                            font-size: 14px;
                            font-weight: 600;
                            color: var(--text_color);
                            display: flex;
                            justify-content: space-between;
                            align-items: center;
                        }
                        p {
                            font-size: 14px;
                            color: $home_body;
                            font-weight: 400;
                            margin-bottom: 0;
                        }
                    }
                }
            }
        }
    }
}

.shop_tab {
    border-bottom: 1px solid #ebebeb;
    li {
        a {
            font-size: 20px;
            font-weight: 600;
            color: var(--text_color);
            position: relative;
            padding: 10px;
            &::before {
                content: "";
                position: absolute;
                right: 0;
                bottom: -1px;
                height: 3px;
                width: 100%;
                background: var(--base_color);
                transition: 0.3s;
                opacity: 0;
            }
            &.active,
            &:hover {
                color: var(--text_color);
                &::before {
                    opacity: 1;
                }
            }
        }
    }
}

