                font-size: 14px;
                color: $home_body;
                line-height: 26px;
                span {
                    font-weight: 600;
                    color: var(--text_color);
                }
                &:not(:last-child) {
                    margin-bottom: 5px;
                }
            }
        }
    }
    .course_cutomer_reviews {
        padding: 50px 50px;
        @media #{$tab} {
            padding: 20px;
        }
        .details_title {
            margin-bottom: 28px;
        }
    }
}

.single_reviews {
    display: flex;
    border-bottom: 1px solid #ebebeb;
    padding-bottom: 35px;
    margin-bottom: 40px;
    .thumb {
        font-size: 20px;
        font-weight: 700;
        font-family: $font1;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        background: var(--base_color);
