        text-transform: uppercase;
        text-align: center;
        line-height: 80px;
        margin-bottom: 20px;
    }
    &:last-child {
        padding-bottom: 0px;
        border: 0;
        margin-bottom: 5px;
    }
    // @media #{$tab} {
    //     padding-bottom: 40px;
    //     margin-bottom: 40px;
    // }
    @media #{$small_mobile} {
        flex-direction: column;
    }
    .review_content {
        h4 {
            margin-bottom: 1px;
            color: var(--text_color);
        }
        .review_content_head {
            margin: 0px 0 23px 0;
            grid-gap: 20px;
        }
        .rated_customer {
            display: flex;
            align-items: center;
            margin: 7px 0 0px 0;
            .feedmak_stars {
                display: flex;
                align-items: center;
                margin: 0;
                margin-left: 15px;
                i {
                    color: #ffc107;
                    font-size: 15px;
                    &:not(:last-child) {
                        margin-left: 5px;
                    }
                }
            }
            span {
                font-size: 14px;
                font-weight: 500;
                color: var(--text_color);
                line-height: 1;
            }
        }
        p {
            font-size: 16px;
            line-height: 1.63;
            color: $body_text;
        }
    }
}

.seperator_line {
    border-top: 1px solid #ebebeb;
}

// details_tab_style
.details_tab_style {
    display: flex;
    grid-gap: 10px;
    &.style2 {
        flex-direction: column;
        ul {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            flex: 1 1 auto;
            grid-gap: 10px;
        }
    }
    &.style3 {
        flex-direction: column;
        grid-gap: 30px;
    }
    &.style4 {
        grid-gap: 30px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    &.style5 {
        flex-direction: column;
        ul {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            flex: 1 1 auto;
            grid-gap: 10px;
            @media #{$tab} {
                grid-template-columns: repeat(4, 1fr);
            }
        }
    }
    @media #{$tab} {
        flex-direction: column;
    }
    ul {
        display: flex;
        grid-gap: 10px;
        flex: 120px 0 0;
        @media #{$tab} {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            flex: 1 1 auto;
        }
        li {
            @media #{$tab} {
                flex: 20% 0 0;
            }
            a {
                padding: 0;
                background: #f5f5f5;
                img {
                    width: 100%;
                }
            }
        }
    }
    .details_tab_content {
        background: #f5f5f5;
        .tab-pane {
            @media #{$tab} {
                text-align: center;
            }
        }
        img {
            width: 100%;
            @media #{$tab} {
                height: 250px;
                width: auto;
            }
        }
    }
}

.home_details_tab {
    background: #f8f8fe;
    border-radius: 10px;
    display: inline-flex;
    @media #{$small_mobile} {
        display: flex;
    }
    li {
        @media #{$small_mobile} {
            display: block;
            width: 100%;
        }
        a {
            font-size: 16px;
            font-weight: 700;
            color: var(--text_color);
            font-family: $font1;
            border-radius: 10px;
            display: inline-block;
            padding: 13px 40px;
            text-align: center;
            @media #{$small_mobile} {
                width: 100%;
                display: block;
            }
            &.active {
                background: var(--base_color);
                color: $white;
            }
        }
    }
}

