.amaz_checkout_loginArea {
    display: flex;
    justify-content: center;
    @media #{$tab} {
        padding: 0px 0;
    }
    .login_area_inner {
        max-width: 570px;
        padding: 80px 60px;
        background: #fff;
        @media #{$small_mobile} {
            padding: 50px 30px;
        }
        @media #{$large_mobile} {
            padding: 50px 30px;
        }
        h4 {
            font-size: 26px;
            line-height: 36px;
            color: var(--text_color);
            margin-bottom: 52px;
            font-weight: 700;
            font-family: $font1;
            @media #{$tab} {
                font-size: 20px;
                margin-bottom: 30px;
                line-height: 30px;
            }
        }
        .login_with_links {
            display: flex;
            grid-gap: 10px;
            @media #{$tab} {
                flex-wrap: wrap;
            }
            a {
                background: var(--base_color);
                font-size: 16px;
                font-weight: 500;
                text-transform: capitalize;
                padding: 14.5px 30px;
                color: #fff;
                display: block;
                text-align: center;
                position: relative;
                display: flex;
                align-items: center;
                grid-gap: 7px;
                white-space: nowrap;
                @media #{$tab} {
                    flex: 1 1 auto;
                }
                &.Facebook_bg {
                    background: #395799;
                }
                &.twitter_bg {
                    background: #03abf0;
                }
                i {
                    font-size: 16px;
                    color: #fff;
                }
            }
        }
        .sign_up_text {
            font-size: 14px;
            font-weight: 500;
            color: #373737;
            margin-top: 18px;
            font-family: $font1;
            &.style2 {
                font-family: $font1;
            }
            a {
                color: #4183ff;
            }
        }
    }
    .remember_pass {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 22px;
        .label_name {
            color: $body_text;
        }
        .forgot_pass {
            font-size: 14px;
            font-weight: 500;
            color: #5dabfc;
            &:hover {
                color: var(--base_color);
            }
        }
        p {
            font-size: 14px;
            font-weight: 400;
            color: $body_text;
            a {
                color: $body_text;
                &:hover {
                    color: var(--base_color);
                }
            }
        }
    }
    .primary_line_input {
        background: transparent;
        padding-bottom: 5px;
    }
}

.custom_group_field {
    border-bottom: 1px solid var(--border_color);
    span {
        background: transparent;
        border: 0;
        padding: 0;
        width: 20px;
        text-align: right;
        padding-bottom: 9px;
        height: 40px;
    }
    input {
        border: 0;
        font-size: 14px;
        font-weight: 400;
        font-family: $font1;
        box-shadow: none !important;
        padding-bottom: 13px;
        padding-right: 10px;
        color: var(--text_color);
        background: transparent !important;
        box-shadow: none !important;
        &:focus {
            outline: none;
        }
        &::placeholder {
            color: $body_text;
        }
    }
}

.login_modal {
    .close_modal {
        position: absolute;
        left: 15px;
        z-index: 10;
        top: 15px;
        cursor: pointer;
        width: 30px;
        height: 30px;
        text-align: center;
        line-height: 30px;
        font-size: 15px;
        border-radius: 50%;
        transition: 0.3s;
        display: inline-block;
        &:hover {
            background: var(--base_color);
            color: #fff;
            transform: rotate(90deg);
        }
        i {
            line-height: 30px;
            font-size: 14px;
        }
    }
    .modal-content {
        border: 0;
    }
    .infix_login_area {
        padding: 0;
    }
    .modal-body {
        padding: 0;
    }
    .modal-dialog {
        max-width: 690px;
    }
    .modal-dialog.style2 {
        max-width: 570px;
    }
}

.about_modal {
    .modal-dialog {
        max-width: 590px;
    }
    .infix_login_area {
        .login_area_inner {
            padding: 50px;
            max-width: 100%;
        }
    }
}

.shiping_modal {
    .modal-dialog {
        max-width: 900px;
    }
    .infix_login_area {
        .login_area_inner {
            padding: 50px;
            max-width: 100%;
        }
    }
}

.size_modal {
    .modal-dialog {
        max-width: 942px;
    }
    .infix_login_area {
        .login_area_inner {
            padding: 50px 40px;
            max-width: 100%;
        }
    }
}

.form_sep {
    grid-gap: 10px;
    margin: 29px 0 28px 0;
    .sep_line {
        height: 2px;
        background: #f1ece8;
    }
    .form_sep_text {
        color: var(--text_color);
    }
}

.form_sep2 {
    grid-gap: 20px;
    margin: 17px 0 35px 0;
    .sep_line {
        height: 1px;
        background: #f1ece8;
    }
    .form_sep_text {
        color: $body_text;
    }
}

.amazy_login_area {
    min-height: 100vh;
    display: grid;
    grid-template-columns: 745px auto;
    @media #{$tab} {
        display: block;
    }
    @media #{$medium_device} {
        grid-template-columns: 410px auto;
    }
    @media #{$lg_device} {
        grid-template-columns: 500px auto;
    }
    @media #{$large_xl} {
        grid-template-columns: 550px auto;
    }
    .amazy_login_area_left {
        padding: 40px 30px;
        .amazy_login_form {
            max-width: 400px;
            @media #{$medium_device} {
                max-width: 340px;
            }
            h3 {
                font-size: 32px;
                font-weight: 700;
            }
            .support_text {
                font-size: 16px;
                font-weight: 400;
                margin: 8px 0 35px 0;
            }
            .google_logIn {
                border-radius: 5px;
                border: 1px solid var(--border_color);
                background-color: #fff;
                grid-gap: 15px;
                height: 50px;
                &:not(:last-child) {
                    margin-bottom: 16px;
                }
            }
            .sign_up_text {
                font-size: 14px;
                color: var(--text_color);
                font-weight: 500;
                a {
                    color: var(--base_color);
                    &:hover {
                        text-decoration: underline;
                    }
                }
            }
        }
    }
    .amazy_login_area_right {
        padding: 40px 30px;
        @include background( "../img/banner/login_bg_img.png", center center, no-repeat);
        @media #{$tab} {
            display: none !important;
        }
        .amazy_login_area_right_inner {
            .thumb {
                margin-bottom: 52px;
            }
            .login_text {
                max-width: 400px;
                margin: auto;
                h4 {
                    font-size: 40px;
                    font-weight: 700;
                    line-height: 1.25;
                    color: var(--text_color);
                    margin-bottom: 15px;
                }
                p {
                    font-size: 16px;
                    font-weight: 500;
                    line-height: 1.75;
                    color: var(--text_color);
                    max-width: 300px;
                }
            }
        }
    }
}