.primary_checkbox {
    display: inline-block;
    position: relative;
    margin: 0;
    align-items: flex-start;
    // align-items: center;
    h5 {
        font-size: 14px;
        font-weight: 500;
        font-family: $font1;
        color: var(--text_color);
        margin-bottom: 0;
    }
    .label_name {
        font-size: 14px;
        font-weight: 500;
        font-family: $font1;
        color: $home_body;
        margin-bottom: 0;
    }
    &.white_checkbox {
        .checkmark:after {
            border-color: $white;
        }
    }
    .checkmark {
        position: relative;
        width: 18px;
        height: 18px;
        top: 0;
        right: 0;
        display: block;
        cursor: pointer;
        line-height: 18px;
        flex: 18px 0 0;
        border-radius: 3px;
    }
    // .checkmark:after {
    //     content: "";
    //     position: absolute;
    //     display: none;
    // }
    input {
        position: absolute;
        right: 0;
        top: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;
    }
    input:checked~.checkmark:after {
        // background: linear-gradient(90deg, #7C32FF 0.47%, #C738D8 100%);
        width: 100%;
        height: 100%;
        border: 0;
        // box-shadow: 0px 5px 10px rgba(108, 39, 255, 0.25);
        transition: 0.3s;
        transform: scale(0);
    }
    input:checked~.checkmark {
        background: var(--base_color);
        // width: 100%;
        // height: 100%;
        transition: 0.3s;
    }
    input:checked~.checkmark::before {
        content: "\e64c";
        font-family: "themify";
        position: absolute;
        display: block;
        top: 0px;
        right: 3px;
        text-indent: 1px;
        color: #828bb2;
        background-color: transparent;
        border: 0px;
        -webkit-transform: rotate(8deg);
        -moz-transform: rotate(8deg);
        -ms-transform: rotate(8deg);
        -o-transform: rotate(8deg);
        transform: rotate(8deg);
        font-size: 10px;
        font-weight: 600;
        line-height: 18px;
        z-index: 99;
        color: #fff;
        transition: 0.3s;
    }
    .checkmark:after {
        position: absolute;
        display: block;
        top: 0;
        right: 0;
        content: "";
        width: 18px;
        height: 18px;
        background: transparent;
        border-radius: 3px;
        border: 1px solid var(--border_color);
        transition: 0.3s;
        transform: scale(1);
    }
    &.style2 {
        .checkmark:after,
        .checkmark {
            border-radius: 50%;
        }
        .checkmark {
            width: 15px;
            height: 15px;
            line-height: 15px;
            flex: 15px 0 0;
        }
        input:checked~.checkmark::before {
            top: 0px;
            right: 3px;
            border: 0px;
            font-size: 8px;
            line-height: 15px;
        }
        .checkmark:after {
            width: 15px;
            height: 15px;
        }
    }
    &.style3 {
        .checkmark:after,
        .checkmark {
            border-radius: 50%;
        }
        .checkmark {
            width: 20px;
            height: 20px;
            line-height: 20px;
            flex: 20px 0 0;
        }
        input:checked~.checkmark::before {
            top: 0px;
            right: 3px;
            border: 0px;
            font-size: 11px;
            line-height: 20px;
        }
        .checkmark:after {
            width: 20px;
            height: 20px;
        }
        .label_name {
            font-size: 16px;
            font-weight: 500;
            color: #00124e;
        }
    }
    &.style4 {
        .checkmark:after,
        .checkmark {
            border-radius: 50%;
        }
        .checkmark {
            width: 19px;
            height: 19px;
            line-height: 19px;
            flex: 19px 0 0;
        }
        input:checked~.checkmark::before {
            top: 0px;
            right: 3px;
            border: 0px;
            font-size: 11px;
            line-height: 19px;
        }
        .checkmark:after {
            width: 19px;
            height: 19px;
            border: 5px solid #687083;
        }
        .label_name {
            font-size: 14px;
            font-weight: 500;
            color: $body_text;
        }
    }
    &.style5 {
        .checkmark:after,
        .checkmark {
            border-radius: 50%;
        }
        .checkmark {
            width: 19px;
            height: 19px;
            line-height: 19px;
            flex: 19px 0 0;
        }
        input:checked~.checkmark::before {
            top: 0px;
            right: 3px;
            border: 0px;
            font-size: 11px;
            line-height: 19px;
            display: none;
        }
        input:checked~.checkmark::after {
            border: 5px solid #222222;
            transform: scale(1);
        }
        input:checked~.checkmark {
            background: transparent;
        }
        .checkmark:after {
            width: 19px;
            height: 19px;
            border: 2px solid #e3e6ef;
        }
        .checkmark:before {
            display: none;
        }
        .label_name {
            font-size: 14px;
            font-weight: 500;
            color: #222;
            position: relative;
            top: 1px;
        }
    }
    &.style6 {
        .checkmark:after,
        .checkmark {
            border-radius: 50%;
        }
        .checkmark {
            width: 18px;
            height: 18px;
            line-height: 18px;
            flex: 18px 0 0;
            background: #fff !important;
            border: 1px solid #e1e2e6;
        }
        input:checked~.checkmark::after {
            width: 10px;
            height: 10px;
            border: 0;
            transition: 0.3s;
            transform: scale(1);
            background: red;
            border: 0 !important;
            right: 3px;
            top: 3px;
        }
        input:checked~.checkmark {
            border: 1px solid var(--base_color);
        }
        input:checked~.checkmark::before {
            display: none;
        }
        .checkmark:after {
            width: 10px;
            height: 10px;
            right: 3px;
            top: 3px;
            border: 0 !important;
        }
        .checkmark::before {
            display: none;
        }
        .label_name {
            font-size: 14px;
            font-weight: 500;
            color: #687083;
        }
    }
}

.round_checkbox {
