        }
        .checkmark::before {
            background: #81d742;
        }
    }
    &.brouwn_check {
        input:checked~.checkmark {
            border-color: #dd9a32;
        }
        .checkmark::before {
            background: #dd9a32;
        }
    }
    &.violate_check {
        input:checked~.checkmark {
            border-color: #8224e2;
        }
        .checkmark::before {
            background: #8224e2;
        }
