    &.red_check {
        input:checked~.checkmark {
            border-color: #ef3736;
        }
        .checkmark::before {
            background: #ef3736;
        }
    }
    &.yellow_check {
        input:checked~.checkmark {
            border-color: #eeee22;
        }
        .checkmark::before {
            background: #eeee22;
        }
    }
}

