.play_button {
    width: 100px;
    height: 100px;
    background: #fff;
    border-radius: 50%;
    color: var(--text_color);
    display: inline-block;
    text-align: center;
    line-height: 100px;
    position: relative;
    font-size: 24px;
    transition: 0.3s;
    cursor: pointer;
    &:hover {
        background: var(--base_color);
        color: $white;
    }
}

.play_button2 {
    width: 80px;
    height: 80px;
    background: transparent;
    border-radius: 50%;
    color: var(--base_color);
    display: inline-block;
    text-align: center;
    line-height: 80px;
    position: relative;
    font-size: 13px;
    transition: 0.3s;
    cursor: pointer;
    border: 1.5px solid var(--base_color);
    &:hover {
        background: var(--base_color);
        color: $white;
    }
}

.theme__overlay {
    position: relative;
    z-index: 2;
    &:before {
        content: "";
        position: absolute;
        top: 0;
        right: 0;
        width: 100%;
        height: 100%;
        background: #0a0b21;
        opacity: 0.4;
        z-index: -1;
    }
}

.lms_tabmenu {
    background: #f8f8fe;
    border-radius: 10px;
    display: inline-flex;
    li {
        a {
            font-size: 16px;
            font-weight: 600;
            font-family: $font1;
            color: var(--text_color);
            border-radius: 10px;
            padding: 13px 39px;
            @media #{$tab} {
                padding: 13px 30px;
            }
            @media #{$large_mobile} {
                padding: 13px 27px;
            }
            &.active {
                background: var(--base_color);
                color: $white;
                border-radius: 10px;
            }
        }
    }
}

.theme_border {
    border-top: 1px solid #e9e7f7;
}

.theme_according {
    button {
        &::before {
            right: 5px;
            position: absolute;
            -webkit-transition: 0.4s;
            transition: 0.4s;
            content: "\e622";
            font-family: "themify";
            font-size: 10px;
            color: #090b17;
            font-weight: 900;
            top: 17px;
        }
        &.collapsed::before {
            content: "\e61a";
            font-family: "themify";
        }
    }
}

.theme_according {
    .card {
        margin-bottom: 15px;
        border: 0;
        .card-header {
            border: 0;
            border-bottom: 1px solid var(--border_color);
            border-radius: 0px;
            background: transparent;
            padding: 0;
            h5 {
                button {
                    font-family: $font1;
                    font-size: 16px;
                    font-weight: 500;
                    color: var(--text_color);
                    display: flex;
                    justify-content: space-between;
                    width: 100%;
                    border: 0;
                    padding: 11.5px 30px 13px 25px;
                    text-decoration: none !important;
                    text-align: right;
                    box-shadow: none;
                    @media #{$tab} {
                        flex-wrap: wrap;
                    }
                    span {
                        font-size: 14px;
                        font-weight: 500;
                        font-family: $font1;
                        color: var(--text_color);
                    }
                }
            }
        }
    }
    .card-body {
        padding: 0;
        padding-top: 0px;
        background: #f9f9fd;
        padding: 30px 55px 36px 65px;
        margin-top: 30px;
        @media #{$tab} {
            padding: 30px 48px 36px 25px;
        }
        h5 {
            font-size: 16px;
            font-weight: 500;
            color: var(--text_color);
            margin-bottom: 15px;
            position: relative;
            i {
                position: absolute;
                top: 4px;
                font-size: 10px;
                color: var(--text_color);
                right: -20px;
            }
        }
        p {
            font-size: 14px;
            font-weight: 400;
            color: $home_body;
            line-height: 1.86;
            margin-bottom: 0;
        }
    }
}

