footer.home_three_footer {
    background-color: var(--footer_background_color);
    .footer_top_area {
        padding-top: 50px;
        padding-bottom: 55px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.05);
    }
    .main_footer_wrap {
        padding-top: 100px;
        padding-bottom: 24px;
        @media #{$tab} {
            padding-top: 70px;
            padding-bottom: 24px;
        }
        .footer_widget {
            margin-bottom: 30px;
            .footer_logo {
                margin-bottom: 10px;
            }
            .footer_title {
                h3 {
                    font-family: $font1;
                    font-size: 16px;
                    font-weight: 700;
                    margin-bottom: 16px;
                    text-transform: uppercase;
                    color: var(--footer_text_color);
                }
            }
            .footer_links {
                li {
                    a {
                        font-size: 16px;
                        font-weight: 400;
                        color: var(--footer_text_color);
                        font-family: $font1;
                        line-height: 1.88;
                        &:hover {
                            color: var(--base_color);
                        }
                    }
                }
            }
            .address_list {
                max-width: 270px;
                p {
                    font-size: 14px;
                    color: #656565;
                    margin-bottom: 0;
                    line-height: 1.86;
                    font-weight: 500;
                    margin-bottom: 15px;
                    &:not(:last-child) {
                        margin-bottom: 0;
                    }
                    span {
                        font-weight: 700;
                    }
                }
            }
            .large_address {
                font-size: 40px;
                font-weight: 700;
                color: $white;
                margin-bottom: 30px;
                display: flex;
                align-items: flex-end;
                line-height: 0.75;
                grid-gap: 10px;
                @media #{$small_mobile} {
                    font-size: 30px;
                }
                span {
                    font-size: 20px;
                    font-weight: 500;
                    margin-right: 5px;
                    display: inline-block;
                }
            }
            .footer_opening_time {
                margin: 15px 0 11px 0;
                padding-bottom: 17px;
                border-bottom: 1px solid #151515;
                p {
                    font-size: 13px;
                    font-weight: 500;
                    line-height: 1.77;
                    color: #666666;
                    text-transform: uppercase;
                }
            }
            .help_text {
                p {
                    font-size: 16px;
                    font-weight: 500;
                    color: $white;
                    a {
                        color: var(--base_color);
                    }
                }
            }
            .address_text {
                font-size: 16px;
                font-weight: 400;
                font-family: $font1;
                color: #666666;
                margin-bottom: 19px;
                line-height: 1.63;
                max-width: 226px;
            }
            .social__Links {
                a {
                    color: #808080;
                    &:hover {
                        color: var(--base_color);
                    }
                }
            }
        }
    }
    .copyright_area {
        padding-bottom: 28px;
        .footer_border {
            border-top: 1px solid #151515;
            margin-bottom: 23px;
        }
        .copy_right_text {
            padding: 17px 0;
            p {
                margin-bottom: 0;
                font-size: 14px;
                color: #687083;
                font-weight: 500;
                font-family: $font1;
                text-transform: capitalize;
                a {
                    color: var(--base_color);
                }
            }
            .footer_list_links {
                display: flex;
                grid-gap: 35px;
                align-items: center;
                flex-wrap: wrap;
                @media #{$tab} {
                    grid-gap: 15px;
                }
                a {
                    font-size: 14px;
                    font-weight: 500;
                    color: $white;
                    &:hover {
                        color: var(--base_color);
                    }
                }
            }
        }
        .payment_imgs {
            margin: 20px 0;
        }
    }
}

.google_play_box {
    background: var(--base_color);
    border-radius: 5px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.05);
    padding: 11px 30px;
    grid-gap: 15px;
    min-width: 215px;
    max-width: 215px;
    &:hover {
        background: var(--text_color);
    }
    .google_play_text {
        span {
            font-size: 12px;
            font-weight: 500;
            text-transform: uppercase;
            color: $white;
            line-height: 1;
            display: block;
            margin-bottom: 6px;
        }
        h4 {
            font-size: 20px;
            font-weight: 700;
            color: $white;
            line-height: 1;
            margin: 0;
        }
    }
}