.social__Links {
    a {
        font-size: 16px;
        color: #5b6c8f;
        &:not(:last-child) {
            margin-left: 25px;
        }
        &:hover {
            color: var(--base_color);
        }
    }
    &.style2 {
        a {
            color: $home_body;
            &:hover {
                color: var(--base_color);
            }
        }
    }
}

.social__Links2 {
    a {
        font-size: 16px;
        color: white;
        background-color: #222222;
        width: 45px;
        height: 45px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        &:not(:last-child) {
            margin-left: 10px;
            @media #{$lg_device} {
                margin-bottom: 10px;
            }
        }
        &:hover {
            background: var(--base_color);
            color: $white;
        }
    }
}

.social__Links3 {
    a {
        font-size: 16px;
        color: $white;
        &:not(:last-child) {
            margin-left: 25px;
        }
        &:hover {
            color: var(--base_color);
        }
    }
}

.social__Links4 {
    a {
        font-size: 16px;
        color: white;
        background-color: var(--base_color);
        width: 45px;
        height: 45px;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        &:not(:last-child) {
            margin-left: 10px;
            @media #{$lg_device} {
                margin-bottom: 10px;
            }
        }
        &:hover {
            background: var(--text_color);
            color: $white;
        }
    }
}

.social__Links5 {
    a {
        font-size: 14px;
        color: #687083;
        position: relative;
        padding: 0 15px;
        display: inline-block;
        &:not(:last-child) {
            margin-left: 0px;
            &::before {
                content: "";
                position: absolute;
                left: 0;
                top: 50%;
                transform: translateY(-50%);
                height: 15px;
                background: #f1f1f1;
                width: 1px;
            }
        }
        &:hover {
            color: var(--base_color);
        }
    }
    &.style_2 {
        a {
            color: $white;
            &:not(:last-child) {
                &::before {
                    background: rgba(241, 241, 241, 0.1);
                }
            }
        }
    }
    &.style_3 {
        a {
            color: $white;
            &:not(:last-child) {
                &::before {
                    background: #4b505d;
                    height: 10px;
                }
            }
        }
    }
}

.instagram_wrapper {
    position: relative;
    z-index: 10;
    &.style2 {
        .insta_title {
            h3 {
                font-size: 20px;
                color: var(--text_color);
                font-family: $font1;
                span {
                    font-size: 30px;
                    font-weight: 700;
                    color: var(--base_color);
                }
            }
        }
    }
    &.style3 {
        .insta_title {
            h3 {
                font-size: 20px;
                color: var(--text_color);
                font-family: $font1;
                span {
                    font-size: 30px;
                    font-weight: 700;
                    color: var(--text_color);
                }
            }
        }
    }
    &.style4 {
        .insta_title {
            h3 {
                font-size: 20px;
                color: var(--text_color);
                font-family: $font1;
                span {
                    font-size: 30px;
                    font-weight: 700;
                    color: var(--base_color);
                }
            }
        }
    }
    .insta_title {
        h3 {
            font-size: 20px;
            color: var(--text_color);
            font-family: $font2;
            span {
                font-size: 30px;
                font-weight: 700;
                color: var(--base_color);
            }
        }
    }
    .insta_title2 {
        h3 {
            font-size: 20px;
            color: var(--text_color);
            font-family: $font1;
            span {
                font-size: 30px;
                font-weight: 700;
                color: var(--base_color);
            }
        }
    }
    .instagram_grids {
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(6, 1fr);
        @media #{$tab} {
            grid-template-columns: repeat(3, 1fr);
        }
        @media #{$medium_device} {
            grid-template-columns: repeat(3, 1fr);
        }
        .single_thumb {
            display: block;
            img {
                width: 100%;
            }
        }
    }
}

.theme_mailChimp {
    // height: 50px;
    background: transparent;
    flex-wrap: nowrap;
    border-radius: 5px;
    position: relative;
    form {
        display: grid;
        grid-template-columns: auto 120px;
        grid-gap: 0px;
        border: 0;
        border-radius: 0px;
        @media #{$small_mobile} {
            grid-template-columns: auto;
        }
    }
    .form-control {
        background: $white;
        color: var(--text_color);
        font-weight: 400;
        padding-right: 20px;
        border-radius: 0;
        height: 50px;
        border: 0;
        font-family: $font1;
        font-size: 14px;
        border: 1px solid var(--border_color);
        border-left: 0;
        @media #{$small_mobile} {
            border-radius: 5px;
        }
        &::placeholder {
            color: #676e8b;
            font-size: 14px;
        }
        &:focus {
            box-shadow: none !important;
            z-index: 1;
        }
    }
    button {
        font-size: 14px;
        font-weight: 500;
        color: $white;
        background: var(--base_color);
        text-transform: capitalize;
        border-radius: 0px !important;
        padding: 0 18px;
        box-shadow: none !important;
        // width: 100px;
        position: relative;
        left: 0;
        top: 0;
        border: 0;
        height: 50px;
        font-family: $font1;
        @media #{$small_mobile} {
            border-radius: 5px !important;
        }
    }
    .info {
        font-size: 14px;
        font-weight: 500;
        color: var(--text_color);
        position: absolute;
        right: 0;
        top: 110%;
    }
}

