    .menu_search_popup {
        position: absolute;
        right: 0;
        top: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        background: $white;
        width: 100%;
        height: 100%;
        transform: translateY(-100%);
        transition: 0.3s;
        z-index: 12000000000;
        top: -100%;
        opacity: 0;
        visibility: hidden;
        &.active {
            transform: translateY(0%);
            top: 0;
            opacity: 1;
            visibility: visible;
        }
        .search_close {
            position: absolute;
            left: 16%;
            top: 30px;
            font-size: 20px;
            color: var(--text_color);
            cursor: pointer;
            transition: 0.3s;
            width: 20px;
            height: 20px;
            border: 1px solid var(--text_color);
            border-radius: 50%;
            font-size: 10px;
            font-weight: 900;
