                flex: 70% 0 0;
                max-width: 70%;
            }
            input {
                border: 0;
                border-bottom: 1px solid #d9d9d9;
                width: 100%;
                font-size: 14px;
                color: var(--text_color);
                padding: 10px 20px 10px 0px;
                font-weight: 400;
                &::placeholder {
                    color: #6e446e;
                }
                &:focus {
                    border-color: var(--text_color);
                }
            }
            button {
                background: transparent;
                border: 0;
                position: absolute;
                left: 0;
                top: 0;
                padding: 0;
                height: 100%;
                width: 30px;
                line-height: 1;
                text-align: left;
                i {
                    font-size: 18px;
                    color: var(--text_color);
                    font-weight: 700;
                }
            }
        }
    }
