.blog_details_area {
    padding: 100px 0 100px 0;
    @media #{$tab} {
        padding: 70px 0 70px 0;
    }
    .blog_details_inner {
        .blog_post_date {
            font-size: 14px;
            font-weight: 500;
            color: var(--text_color);
            font-family: $font1;
            grid-gap: 10px;
            span {
                font-size: 12px;
                font-weight: 700;
                color: var(--base_color);
                border-radius: 5px;
                background-color: var(--base_color_20);
                padding: 3px 11px;
                text-transform: uppercase;
                display: inline-block;
            }
            p {
                font-size: 14px;
                font-weight: 400;
                color: $body_text;
            }
        }
        h3 {
            font-size: 30px;
            font-weight: 700;
            line-height: 1.33;
            max-width: 487px;
            margin: 20px 0 32px 0;
            @media #{$tab} {
                font-size: 25px;
            }
        }
        p {
            font-size: 16px;
            color: $home_body;
            font-weight: 400;
            line-height: 1.63;
        }
        .blog_details_banner {
            margin-bottom: 30px;
        }
        .quote_text {
            display: flex;
            margin: 64px 0 40px 0;
            .horizontal_line {
                background: var(--base_color);
                height: 3px;
                width: 90px;
                flex: 90px 0 0;
                position: relative;
                top: 6px;
                margin-left: 30px;
                @media #{$tab} {
                    width: 40px;
                    flex: 40px 0 0;
                    margin-left: 15px;
                }
            }
            h4 {
                font-size: 20px;
                font-weight: 700;
                color: var(--text_color);
                line-height: 1.5;
            }
        }
        .details_info {
            margin-top: 55px;
            h4 {
                font-size: 24px;
                font-weight: 700;
                color: var(--text_color);
                margin-bottom: 10px;
                line-height: 2.08;
                @media #{$tab} {
                    font-size: 20px;
                }
            }
        }
    }
    .blog_reply_box {
        border-top: 1px solid var(--border_color);
        padding-top: 60px;
        margin-top: 56px;
        @media #{$tab} {
            padding-top: 30px;
            margin-top: 36px;
        }
    }
    .blog_details_tags {
        border-top: 1px solid var(--border_color);
        border-bottom: 1px solid var(--border_color);
        padding: 15px 0 54px 0;
        margin: 55px 0 57px 0;
        @media #{$tab} {
            margin: 30px 0 30px 0;
            padding: 15px 0 30px 0;
        }
    }
}