.brand_area {
    .single_brand {
        text-align: center;
        border: 1px solid #dedede;
        height: 160px;
        img {
            display: inline-block;
            width: auto;
        }
    }
}

.dash_border {
    border-top: 1px dashed #ebebeb;
}

// cta
.cta_area3 {
    background: #f7f7f7;
    padding-bottom: 100px;
    @media #{$tab} {
        padding-bottom: 75px;
    }
    .cta_inner {
        position: relative;
        z-index: 1;
        background-image: url(../img/banner/cta_bg.jpg);
        background-repeat: no-repeat;
        background-position: center center;
        background-size: cover;
        padding: 48px 90px 48px 70px;
        display: grid;
        grid-template-columns: 7fr 5fr;
        align-items: center;
        @media #{ '(min-width: 768px) and (max-width: 1199.98px)'} {
            padding: 50px 30px;
            grid-template-columns: repeat(1, 1fr);
            grid-gap: 30px;
        }
        @media #{$tab} {
            grid-template-columns: 1fr;
            padding: 30px;
        }
        .cta_inner_subscribe {
            padding-left: 50px;
            @media #{ '(min-width: 768px) and (max-width: 1199.98px)'} {
                padding-left: 0;
            }
            @media #{$tab} {
                padding-left: 0;
            }
        }
        &::before {
            content: "";
            position: absolute;
            right: 10px;
            top: 10px;
            left: 10px;
            background: #fff;
            bottom: 10px;
            z-index: -1;
        }
        .cta_inner_text {
            display: flex;
            @media #{$tab} {
                display: block;
            }
            h4 {
                font-size: 24px;
                font-weight: 700;
                line-height: 1.33;
                text-align: left;
                margin-bottom: 0;
                font-family: $font1;
                @media #{$tab} {
                    font-size: 20px;
                    text-align: right;
                }
                span {
                    display: block;
                    color: var(--base_color);
                }
            }
            .cta_text {
                position: relative;
                padding-right: 30px;
                margin-right: 30px;
                @media #{$tab} {
                    padding: 0;
                    margin: 0;
                    margin-top: 20px;
                    margin-bottom: 20px;
                }
                &::before {
                    position: absolute;
                    right: 0;
                    top: 0;
                    content: "";
                    height: 63px;
                    width: 4px;
                    background: #f5f5f5;
                    @media #{$tab} {
                        display: none;
                    }
                }
                p {
                    font-size: 16px;
                    font-weight: 400;
                    color: $home_body;
                    line-height: 2;
                    margin-bottom: 0;
                }
            }
        }
    }
}

